<template>
  <div>
    <header class="header-area" id="Header">
      <div class="header-center">
        <label class="header-logo-txt" @click="PageHome()"><!-- Ian En --></label>
        <!-- <img src="@/components/header/img/logo.png" alt="超現實logo" class="header-logo" @click="PageHome()" /> -->
        <button :class="MenuBtnClass" @click="MenuSwitch()">
          <span></span>
        </button>
        <div :class="MenuClass">
          <button :class="MenuItemClass('Home')" @click="PageHome()">Home</button>
          <button :class="MenuItemClass('PortfolioList')" @click="PagePortfolioList()">Portfolio</button>
        </div>
      </div>
    </header>
    <div class="header-position"></div>
  </div>
</template>

<script>
import '@/components/header/header.scss';

export default {
  name: 'Header',
  props: {},
  data() {
    return {
      nowTab: '',
      nowApp: '',
      MenuShow: false,
    };
  },
  mounted() {
    this.nowTab = this.GetRouteName();
  },
  computed: {
    MenuBtnClass() {
      return this.MenuShow ? 'header-menu-btn active' : 'header-menu-btn';
    },
    MenuClass() {
      return this.MenuShow ? 'header-menu-show' : 'header-menu-close';
    },
  },
  methods: {
    GetRouteName() {
      return this.$router.currentRoute.name;
    },
    MenuSwitch() {
      this.MenuShow = !this.MenuShow;
    },
    MenuItemClass(menuName) {
      return this.nowTab == menuName ? 'menu-item-select' : 'menu-item';
    },
    PageHome() {
      this.MenuShow = false;
      if (this.nowTab != 'Home') {
        this.$router.push({ path: '/page/Home' });
        this.nowTab = 'Home';
      }
      this.ScrollTO('Header');
    },
    PageAboutUs() {
      this.MenuShow = false;
      if (this.nowTab != 'AboutUS') {
        this.$router.push({ path: '/AboutUS' });
        this.nowTab = 'AboutUS';
      }
      this.ScrollTO('Header');
    },
    PagePortfolioList() {
      this.MenuShow = false;
      if (this.nowTab != 'PortfolioList') {
        this.$router.push({ path: '/page/PortfolioList' });
        this.nowTab = 'PortfolioList';
      }
      this.ScrollTO('Header');
    },
  },
  components: {},
};
</script>
